@import "../src/scss/themes/theme-dark.scss";
@import "../src/scss/themes/theme-light.scss";

html {
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

.content {
  position: relative;
  z-index: 0;
}

.aligner {
  display: flex;
  align-items: center;
  justify-content: center;
}

.title-container {
  height: 50px;
}

.title-styles {
  font-family: "Raleway", sans-serif;
  font-size: 250%;
}

//* PROFILE PICTURE (Moves it more to the right & increases size) */
.profile-container {
  flex: 1;
  display: flex;
  justify-content: flex-end; /* Moves the profile image more to the right */
  align-items: center;
}

/* INCREASED PROFILE PICTURE SIZE */
.profile-frame {
  width: 320px; /* Increased from 250px */
  height: 320px; /* Increased from 250px */
  background: white;
  border-radius: 50%;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}
.profile-pic {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

/* HEADER */
header {
  position: relative;
  width: 100%;
  background-size: cover;
  text-align: left;
  overflow: hidden;
  background-color: #e9d5a1;
  padding: 20px 50px;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: flex-end; /* Align everything more to the right */
  width: 90%;
  margin-left: auto; /* Push everything towards the right */
  padding: 120px 100px; /* Increased padding to shift further right */
  gap: 80px; /* Adds space between the profile picture and text */
}

/* HEADER TEXT SECTION */
.header-text {
  flex: 2;
  text-align: left;
  padding-left: 70px; /* Increased spacing from the profile */
}

.header-name {
  font-size: 36px;
  font-weight: bold;
  color: black;
  font-family: "Poppins", sans-serif;
  margin-bottom: 10px;
}

/* SOCIAL MEDIA ICONS */
.social-icons {
  margin-top: 20px;
}

.social-icons a {
  margin: 0 15px;
  font-size: 28px;
  color: black;
  transition: color 0.3s ease-in-out;
}

.social-icons a:hover {
  color: #007bff;
}

.switch-icon {
  font-size: 20px;
  color: #fff;
  text-align: center;
  width: 100%;
}

.project-date {
  font-size: 16px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  padding: 8px 15px 2px 15px;
  position: relative;
  z-index: -500;
  border-top: 5px solid #696969;
  border-radius: 0 0 5px 5px;
  background-color: #696969;
  color: white;
}

.skills-tile {
  background-color: transparentize(#f8f5f4, 0.95);
  padding: 10px 10px 5px 10px;
  width: 100px;
  margin: 5px 0 5px 0;
  border-radius: 8px;
}

.slider-image {
  border: 5px solid #d7caaa;
}

.slider-tab {
  background-color: #d7caaa;
  height: 25px;
}

.slider-iconfiy {
  margin-top: 10px;
}

.styles_typicalWrapper__1_Uvh::after {
  cursor: none !important;
  display: none;
}

#about {
  background: #fff;
  overflow: hidden;
  padding: 60px 0;
}

/* Container Layout */
.about-content {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  gap: 60px; /* Increase/decrease spacing between image & text as needed */
}

/* Profile Picture Container */
.about-profile-container {
  position: relative;
  width: 350px; /* Increased from 250px */
  height: 350px; /* Increased from 250px */
}

.about-profile-frame {
  width: 350px; /* Match container size */
  height: 350px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
  border: 5px solid rgba(0, 0, 0, 0.1);
  background: radial-gradient(circle at 50% 50%, #ddd 20%, #fff 80%);
  clip-path: polygon(
    50% 0%,
    80% 10%,
    100% 30%,
    95% 70%,
    80% 90%,
    50% 100%,
    20% 90%,
    5% 70%,
    0% 30%,
    20% 10%
  );
}

.about-profile-pic {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

/* Description Box */
.about-description-box {
  max-width: 650px; /* Make the box visibly wider */
  background: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  padding: 25px;
  position: relative;
  right: -50px; /* Moves box slightly right; remove or adjust if unwanted */
  box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.2);
  border-left: 8px solid #efe1bd; /* Accent strip on the left */
}

.about-description-content {
  font-size: 18px;
  line-height: 1.6; /* You can lower this if you want the box to appear less tall */
  font-family: "Lora", serif;
  color: #333;
}

/* Resume Button */
.resume-button {
  background-color: #e3bb55;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 15px;

  transition: 0.3s ease-in-out;
}

.resume-button:hover {
  background-color: black;
  color: #e3bb55; /* Invert colors on hover */
}

.foto {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; /* Ensures consistent height */
}

.foto img {
  display: block;
  width: 100%;
  height: 200px; /* Set a fixed height for consistency */
  object-fit: cover; /* Ensures images maintain aspect ratio */
}

.foto div {
  background: #ffffff;
  display: inline-block;
  margin: 5px;
  padding: 15px;
  text-align: center;
  text-decoration: none;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  transition: all 0.2s linear;
  z-index: 0;
  position: relative;
  width: 600px;
  height: auto;
}

.foto div:after {
  color: #333;
  font-size: 20px;
  content: attr(title);
  position: relative;
  top: 15px;
}

.foto div:hover {
  transform: scale(1.01);
  z-index: 10;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.7);
}

.project-title-settings {
  margin-top: 5%;
  font: 18px/24px "opensans-bold", sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: black;
  text-align: center;
}

.polaroid img {
  display: block;
  width: 300px;
}

.polaroid span {
  background: #ffffff;
  display: inline-block;
  margin: 55px 75px 30px;
  padding: 15px 15px 30px;
  text-align: center;
  text-decoration: none;
  -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  -webkit-transition: all 0.2s linear;
  -moz-transition: all 0.2s linear;
  transition: all 0.2s linear;
  z-index: 0;
  position: relative;
}

.link-href {
  color: black;
}

.wave {
  font-size: 160%;
}

.font-trebuchet {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

#preview {
  width: 500px;
  background-color: #ebeaf5;
  padding: 15px;
  position: relative;
  margin-bottom: 15%;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

#resume {
  background: #efe1bd;
}

.experience-icon {
  font-size: 300%;
  margin-top: 25%;
  text-align: center;
}

.main-badge {
  font-size: 13px !important;
  text-align: left !important;
  padding: 5px 8px 5px 8px !important;
  vertical-align: baseline;
  background-color: #ae944f !important;
  color: white;
  font-weight: lighter !important;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}

.experience-badge {
  font-size: 11px !important;
  text-align: left !important;
  padding: 5px 8px 5px 8px !important;
  vertical-align: baseline;
  background-color: #f9f5e9 !important;
  color: black;
  font-weight: lighter !important;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}

.vertical-timeline-element-date {
  padding: 0 !important;
  margin: 0 !important;
}
/* Coursework Section Styles */
#coursework {
  padding: 50px 0;
  text-align: center;
  background-color: #ebeaf5;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  margin: 40px auto;
  max-width: 800px;
  padding: 40px;
}

#coursework .coursework-title {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 30px;
  color: #222;
  text-transform: uppercase;
  letter-spacing: 2px;
  display: inline-block;
  padding-bottom: 5px;
}

.course-item {
  font-size: 15px;
  color: hsl(0, 0%, 27%);
  margin-bottom: 10px;
  list-style-type: none;
  padding: 8px 0;
  transition: all 0.3s ease-in-out;
}

.course-item:hover {
  color: #222;
  font-weight: bold;
}

h3 {
  text-decoration: underline;
  font-size: 2.8rem;
  font-weight: bold;
  margin-top: 25px;
  color: #111;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  padding-left: 0;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.15);
}

@media only screen and (max-width: 1170px) {
  .experience-icon {
    font-size: 170%;
    margin-top: 27%;
    text-align: center;
  }
}

.modal-inside .modal-content {
  background: white;
}

.bars {
  width: 95%;
  float: left;
  padding: 0;
  text-align: left;
}

.bars .skills {
  margin-top: 36px;
  list-style: none;
}

.bars li {
  position: relative;
  margin-bottom: 60px;
  background: #ccc;
  height: 42px;
  border-radius: 3px;
}

.bars li em {
  font: 15px "opensans-bold", sans-serif;
  color: #313131;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: normal;
  position: relative;
  top: -36px;
}

.bar-expand {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  padding-right: 24px;
  background: #313131;
  display: inline-block;
  height: 42px;
  line-height: 42px;
  border-radius: 3px 0 0 3px;
}

.modal-close {
  text-align: right;
  padding: 10px 15px 10px 15px;
  cursor: pointer;
}

.close-icon {
  color: black;
  font-weight: lighter !important;
}

.modal-description {
  text-align: justify;
  padding: 5px 5px 0 5px;
  margin-bottom: 20px;
  font-size: 12px;
}

.awssld__next {
  outline: none !important;
}

.awssld__prev {
  outline: none !important;
}

.loader-bar-color {
  color: black !important;
}

#portfolio {
  background: #efdabd;
  padding-bottom: 5%;
}

/* Grid Layout for 2x2 Projects */
#portfolio .row {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 2 columns */
  gap: 20px; /* Adds spacing between grid items */
  justify-items: center;
  align-items: center;
}

/* Ensure all items are equal size */
.portfolio-item {
  width: 100%;
  max-width: 350px; /* Set a max-width to maintain uniformity */
}

.portfolio-item {
  max-width: 100%;
  margin-bottom: 15px;
  text-align: center;
}

.portfolio .portfolio-item .portfolio-item-caption {
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  transition: all ease 0.5s;
  opacity: 0;
  background-color: rgba(51, 51, 51, 0.9);
}

.portfolio .portfolio-item .portfolio-item-caption:hover {
  opacity: 1;
}

.portfolio
  .portfolio-item
  .portfolio-item-caption
  .portfolio-item-caption-content {
  font-size: 1.5rem;
}

@media (min-width: 576px) {
  .portfolio .closeButtonResponsive {
    display: block;
  }
  .portfolio .portfolio-item {
    margin-bottom: 30px;
  }
}

#skills {
  background: #1f1f1f;
  min-height: 200px;
  width: 100%;
  overflow: hidden;
  padding-bottom: 10%;
}

.section-title {
  padding-top: 5%;
  padding-bottom: 5%;
  font: 24px "opensans-bold", sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #fff;
  text-align: center;
}

.skill-icon {
  font-size: 180%;
  color: white;
  text-align: center;
  position: relative;
}

footer {
  background: #1f1f1f;
  min-height: 100px;
  width: 100%;
  overflow: hidden;
  font-size: 14px;
  color: white;
  position: relative;
  text-align: center;
}

footer a,
footer a:visited {
  color: #fff;
}

footer a:hover,
footer a:focus {
  color: #fff;
}

.social-links {
  margin-top: 50px;
  font-size: 22px;
}
